.place {
  &__photo--container {
    position: relative;

    box-sizing: border-box;
    width: 100%;
    max-height: 256px;
    overflow: hidden;
    transition: 0.2s;

    @media screen and (min-width: 425px) {
      &:hover {
        transform: scale(1.05);
        border: 2px solid $colorBlue;
        border-radius: 12px;
      }
    }

    &::after {
      content: "";
      display: block;
      padding-bottom: 100%;
    }

    @include onDesktop {
      height: 256px;
      padding-bottom: 0;
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 8px;

    object-fit: cover;
    object-position: center;
  }

  &__photos {
    margin-bottom: 16px;

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__link {
    color: $colorBlue;
    text-decoration: none;
    font-weight: bold;
  }
}
